<template>
  <div id="app">
	  <div class="main">
		  <div class="scrollwrap">
			<div class="head">
				<img class="activityTxt" src="./imgs/headhainan.png" />
				<img class="choujiang" src="./imgs/headtit.png" />
			</div>
			<div class="lottery">
				<img class="lotteryback" src="./imgs/lotteryback.png" alt=""/>
				<div class="lotteryCount">
					<span>您还有2次抽奖机会</span>
				</div>
				<div class="lottermain">
						<div class="item" :class="{'active':index==itemState,'p1':index==1,'p2':index==2,'p3':index==3,'p4':index==4,'p5':index==5,'p6':index==6,'p7':index==7}" v-for="(item,index) in itemList" :key='index'><img class="borders" src="./imgs/activeborder.png" /><img :src="item.imgsrc"/><img class="best" v-if='index==1' src="./imgs/best.png" /></div>
						<div class="item p8" @click="startBtnAction"><img src="./imgs/startBtn.png" /></div>
				</div>
			</div>
			<div class="bounsUserList">
				<img src="./imgs/bounsback.png" alt="">
				<div class="bounsPhonewrap">
					<div class="listwrap">
						<p v-for="(item,index) in bounsList" :key='index' :class="{'best':item.best}"><span class="phonenum">{{item.phone}}</span><span class="bounsname">{{item.bounsname}}</span></p>
					</div>
				</div>
			</div>
			<div class="bounsUserList foot">
				<img src="./imgs/footDesc.png" alt="">
				<div class="bounsPhonewrap">
					<p v-for="(item,index) in footText" :key='index'>{{item.txt}}</p>
				</div>
			</div>
			<div class="footlogo">
				<img src="./imgs/logo.png" alt="">
			</div>
		  </div>
		  <transition name="slideleft">
			<div class="pop_gognzai" v-if='popShowState'>
				<div class="content">
					<div class="close" @click="closePop"><img src="./imgs/close.png" alt=""/></div>
					<div class="poptit">
						<img src="./imgs/wintxt.png" alt="">
					</div>
					<div class="article">
						您抽中了加油卡，分享成功后，还可以再抽一次.
					</div>
					<img src="./imgs/sharebtn.png" class="sharebtn" @click="shareAction" alt="">
				</div>
			</div>
		  </transition>
		  <div class="sharepic" v-if='shareState' @click="shareState = false">
			  <img src="./imgs/sharepic.png" alt=""/>
		  </div>
	  </div>
  </div>
</template>

<script>
export default{
	data(){
		return {
			itemList:[
				{imgsrc:'https://iszhoujy.github.io/promotion/build/src/imgs/gongzai.png'},
				{imgsrc:'https://iszhoujy.github.io/promotion/build/src/imgs/hainan.png'},
				{imgsrc:'https://iszhoujy.github.io/promotion/build/src/imgs/memeda.png'},
				{imgsrc:'https://iszhoujy.github.io/promotion/build/src/imgs/jiayou.png'},
				{imgsrc:'https://iszhoujy.github.io/promotion/build/src/imgs/memeda.png'},
				{imgsrc:'https://iszhoujy.github.io/promotion/build/src/imgs/gongzai.png'},
				{imgsrc:'https://iszhoujy.github.io/promotion/build/src/imgs/jiayou.png'},
				{imgsrc:'https://iszhoujy.github.io/promotion/build/src/imgs/memeda.png'},
			],
			bounsList:[
				{phone:'158****1268',bounsname:'200元加油卡'},
				{phone:'158****3268',bounsname:'大公仔小U'},
				{phone:'158****2268',bounsname:'海南3天2夜游',best:true},
				{phone:'158****4268',bounsname:'大公仔小U'},
				{phone:'158****5268',bounsname:'200元加油卡'},
				{phone:'158****6268',bounsname:'大公仔小U'},
				{phone:'158****6268',bounsname:'大公仔小U'},
			],
			footText:[
				{txt:'1、关注 “ U慧买车公众号 ” 即可参与2次抽奖;'},
				{txt:'2、分享给朋友可再获得2次抽奖机会;'},
				{txt:'3、活动期间如奖品，将抽取完毕则该奖品不再抽取;'},
				{txt:'4、pv活动期为一个月，活动到期后不再参与;'},
				{txt:'5、大奖 “海南3天2晚双飞游” 共有10个名额，抢完即止;'},
			],
			itemState:0,
			lotteryState:false,//是否在抽奖中
			changeTimeSpeed:400,//转动频率
			speedState:{
				state1:false,
				state2:false,
			},
			resultCount:3,
			popShowState:false,
			shareState:false,
		}
	},
	mounted(){
		var listwrap = document.querySelector('.listwrap');
		var self = this;
		var movestate = true;
		function movePhoneList(){
			listwrap.style.transform = 'translateY(-0.5rem)';
			listwrap.addEventListener('transitionend',function(){
				if(movestate){
					movestate = false;
					var firstarr = self.bounsList.shift();
					self.bounsList.push(firstarr);
					listwrap.style.transition = 'none';
					listwrap.style.transform = 'translateY(0)';
				}
			},false);
			setTimeout(function() {
				listwrap.style.transition = 'transform 0.5s';
				movestate = true;
				movePhoneList();
			}, 2000);
		}
		setTimeout(function() {
			movePhoneList();
		}, 2000);
	},
	methods:{
		shareAction(){
			this.closePop();
			this.shareState = true;
		},
		closePop(){
			this.popShowState = false;
		},
		startBtnAction(){
			if(this.lotteryState){
				return false;
			};
			this.longCount = 0;
			this.changeActive();
		},
		showResultBouns(){
			this.popShowState = true;
		},
		changeActive(){
			var self = this;
			this.lotteryState = true;
			this.itemState++;
			if(this.itemState==8){
				this.itemState = 0;
			};
			if(this.changeTimeSpeed>60 && !this.speedState.state1){
				this.changeTimeSpeed-=50;
			};
			if(this.changeTimeSpeed<=60){
				setTimeout(function() {
					self.speedState.state1 = true;
				}, 2000);
				this.changeTimeSpeed = 60;
			};
			if(this.speedState.state1 && this.itemState==this.resultCount){
				this.speedState.state2 = true;
			};
			if(this.speedState.state2){
				this.changeTimeSpeed+=50;
			};
			if(this.changeTimeSpeed>=400){//控制停止时间
				if(this.itemState==this.resultCount){
					this.lotteryState = false;
					this.speedState.state1 = false;
					this.speedState.state2 = false;
					this.changeTimeSpeed = 400;
					this.showResultBouns();
					return false;
				}
			};
			setTimeout(function() {
				self.changeActive();
			}, this.changeTimeSpeed);
		},
	}
}
</script>

<style lang="scss">
@import url("./css/reset.css");
.dialog-fade-enter-active {
	animation: dialog-fade-in .3s
}
.dialog-fade-leave-active {
	animation: dialog-fade-out .3s
}
@keyframes dialog-fade-in {
	0% {
		transform: translate3d(0,-20px,0);
		opacity: 0
	}

	100% {
		transform: translate3d(0,0,0);
		opacity: 1
	}
}
@keyframes dialog-fade-out {
	0% {
		transform: translate3d(0,0,0);
		opacity: 1
	}
	100% {
		transform: translate3d(0,-20px,0);
		opacity: 0
	}
}

.slideleft-enter-active,
.slideleft-leave-active {
	transition: all .8s;
}

.slideleft-enter,
.slideleft-leave-to {
	// transform: scale(0);
	opacity: 0;
}
#app{
	width:100%;
	height:100%;
	background:#ffd600;
}
.main{
	overflow-y:auto;
	overflow-x:hidden;
	width: 100%;
	height: 100%;
	.sharepic{
		position: fixed;
		width: 100%;
		height: 100%;
		background:rgba(0, 0, 0, 0.3);
		top: 0;
		left:0;
		img{
			position: absolute;
			right: 0;
			top: 3vw;
			width: 3.8rem;
		}
	}
	.pop_gognzai{
		position: fixed;
		width: 100%;
		height: 100%;
		background:rgba(0, 0, 0, 0.3);
		top: 0;
		left:0;
		.content{
			width: 80%;
			height: 4.5rem;
			background:white;
			border-radius: 0.2rem;
			padding:0 0.4rem;
			position: fixed;
			top: 20vw;
			left:0;
			right:0;
			bottom: 0;
			margin:auto;
			.article{
				font-size: 0.3rem;
				line-height: 0.6rem;
				margin-top: 0.6rem;
			}
			.sharebtn{
				display:block;
				width: 3.2rem;
				margin:0.4rem auto;
			}
			.poptit{
				text-align: center;
				margin-top: 0.4rem;
				img{
					width:3.3rem;
				}
			}
			.close{
				position: absolute;
				top: -1.5rem;
				height:0.68rem;
				left:50%;
				transform: translateX(-50%);
				img{
					width: 0.68rem;
				}
			}
		}
	}
	.footlogo{
		text-align: center;
		margin: 20vw 0 10vw;
		img{
			width: 1rem;
		}
	};
	.bounsUserList{
		width: 100%;
		margin-top: -10vw;
		position: relative;
		.bounsPhonewrap{
			position: absolute;
			top: 20vw;
			left:6vw;
			right: 6vw;
			bottom: 6vw;
			overflow: hidden;
			.listwrap{
				transition: transform 0.5s;
			}
			p{
				font-size: 0.26rem;
				color: black;
				height: 0.5rem;
				line-height: 0.5rem;
				padding-left:0.4rem;
				&.best{
					color: #fe5467;
				}
				.phonenum{
					display: inline-block;
					width: 3.8rem;
				}
			}
		}
		img{
			width: 95%;
			display: block;
			margin:0 auto;
		}
	}
	.foot{
		margin-top: 0;
		.bounsPhonewrap{
			bottom: 3vw;
			p{
				font-size: 0.2rem;
			}
		}
	}
	.head{
		height: 3.62rem;
		background:url(./imgs/headPic.png) no-repeat center -7px;
		background-size: 100% 100%;
		width:100%;
		padding-top: 1px;
		.activityTxt{
			display: block;
			width: 3.1rem;
			margin:0.3rem auto 0;
		}
		.choujiang{
			width: 100%;
			margin-top: -0.2rem;
		}
	}
	.lottery{
		width: 100%;
		height: 7rem;
		position: relative;
		top: -0.6rem;
		.lottermain{
			width: 68vw;
			height: 68vw;
			position: absolute;
			left: 49.5%;
			transform: translate3d(-50%,0,0);
			top:10.4vw;
			border-radius: 0.3rem;
			.borders{
				position: absolute;
				width: 111%;
				height: 111%;
				left: -1.2vw;
				top: -1.2vw;
				display: none;
			}
			img{
				width: 100%;
				display: block;
			}
			.item{
				position: absolute;
				top:0;
				line-height: 32vw;
				left:0;
				width: 33%;
				height: 33%;
				text-align: center;
				// background:white;
				border-radius: 0.1rem;
				&.active{
					.borders{
						display: block;
					}
				}
				.best{
					position: absolute;
					width: 0.68rem;
					height: 0.55rem;
					z-index: 10;
					top: -2.5vw;
					right: -2.5vw;
				}
			}
			.p1{
				left: 34%;
			}
			.p2{
				left: 68%;
			}
			.p3{
				left: 68%;
				top: 34%;
			}
			.p4{
				left: 68%;
				top: 68%;
			}
			.p5{
				left: 34%;
				top: 68%;
			}
			.p6{
				left: 0%;
				top: 68%;
			}
			.p7{
				left: 0%;
				top: 34%;
			}
			.p8{
				left: 34%;
				top: 34%;
				background:transparent;
				z-index: -1;
			}
		}
		.lotteryback{
			width: 93%;
			top:-0.3rem;
			margin:0 auto;
			position: absolute;
			left:50%;
			transform: translate3d(-50%,0,0)
		}
		.lotteryCount{
			position: absolute;
			width: 2.65rem;
			height: 0.66rem;
			line-height: 0.4rem;
			text-align: center;
			border-radius: 0.3rem;
			border:1px solid #000;
			background:white;
			top: -0.63rem;
			left:50%;
			transform: translate3d(-50%,0,0);
			&:before{
				content:'';
				display: block;
				position: absolute;
				width: 1px;
				height: 0.09rem;
				background:black;
				bottom: -0.09rem;
				left: 0.6rem;
			}
			&:after{
				content:'';
				display: block;
				position: absolute;
				width: 1px;
				height: 0.09rem;
				background:black;
				bottom: -0.09rem;
				right: 0.6rem;
			}

			span{
				font-size: 0.23rem;
			}
		}
	}
}


</style>



